}



.SlideIn-enter {
  -webkit-transform: translateY(-200vh);
          transform: translateY(-200vh);
}
.SlideIn-enter.SlideIn-enter-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: all 0.8s;
  transition: all 0.8s;
}
.SlideIn-exit {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.SlideIn-exit.SlideIn-exit-active {
  /* position: absolute; */
  -webkit-transform: translateY(-400vh);
          transform: translateY(-400vh);
  -webkit-transition: all 0.8s ;
  transition: all 0.8s ;
}

.Navbar-shadow {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 75px;
    background-color: white; 
    box-shadow: 0px 8px 15px 0px rgba(0,0,0,0.5);
    z-index: 2;
}

.Navbar-no-shadow {
    display: flex;
    justify-content: flex-end;
    position: fixed;
    width: 100%;
    height: 105px;
    background-color: white;
    box-shadow: none;
    z-index: 2;
}

.Name {
    font-family: 'Avenir';
    font-weight: 300;
    margin-left: 20px;
    border-style: solid;
    border-width: 1px;
    padding: 6px 12px;
}

.Navbar-Right {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
}

.Navbar-Left {
    display: flex;
