.Navbar-shadow {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 75px;
    background-color: white;
    -webkit-box-shadow: 0px 8px 15px 0px rgba(0,0,0,0.5); 
    box-shadow: 0px 8px 15px 0px rgba(0,0,0,0.5);
    z-index: 2;
}

.Navbar-no-shadow {
    display: flex;
    justify-content: flex-end;
    position: fixed;
    width: 100%;
    height: 105px;
    background-color: white;
    -webkit-box-shadow: none;
    box-shadow: none;
    z-index: 2;
}

.Name {
    font-family: 'Avenir';
    font-weight: 300;
    margin-left: 20px;
    border-style: solid;
    border-width: 1px;
    padding: 6px 12px;
}

.Navbar-Right {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
}

.Navbar-Left {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 100%;

}

@font-face {
    font-family: "Avenir";
    src: url("../Resources/Fonts/Avenir Roman.otf") format("opentype");
}

@media (max-width: 812px) {
    .Navbar-no-shadow {
        display: flex;
        justify-content: center;
        position: fixed;
        width: 100%;
        height: 105px;
        background-color: white;
        box-shadow: none;
        z-index: 2;
    }
    
    .Name {
        font-family: 'Arial';
        font-weight: 300;
        margin-left: 20px;
        border-style: solid;
        border-width: 1px;
        padding: 6px 12px;
    }
    
    .Navbar-Right {
        display: flex;
        /* justify-content: center; */
        align-items: center;
        height: 100%;
    }
    
    .Navbar-Left {
        display: flex;
        /* justify-content: flex-start; */
        align-items: center;
        height: 100%;
    
    }
}